.variations-list {
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width: $min-desktop-width) {
    .variations-list {
        width: 995px;
        margin-bottom: 24px;
    }

    .variations-list__item {
        margin-right: 50px;
        &:last-child {
            margin-right: 0;
        }
    }

    .variations-list__item-link {
        font-size: 16px;
        line-height: 18px;
    }
}